import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [{
    path: '/',
    name: 'Home',
    component: Home,
    // meta: {
    //   requiresAuth: true
    // }
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/Login.vue')
  },
  {
    path: '/home/:id',
    name: 'HomeDetails',
    // meta: {
    //   requiresAuth: true
    // },
    component: () => import('../views/Homedet.vue')
  },
  {
    path: '/crud',
    name: 'Crud',
    component: () => import('../views/Crud.vue')
  },
  {
    path: '/Table',
    name: 'Table',
    // meta: {
    //   requiresAuth: true
    // },
    component: () => import('../views/Table.vue')
  },
  {
    path: '/listpage',
    name: 'List Item',
    // meta: {
    //   requiresAuth: true
    // },
    component: () => import('../views/Listpage.vue')
  },
  {
    path: '/about',
    name: 'About',
    // meta: {
    //   requiresAuth: true
    // },
    component: () => import('../views/About.vue')
  },
]

const router = new VueRouter({
  routes
})

